$normal-gray: #dcdde0;
$bold-gray: #a3a4a5;
$backcolor: white;
.modal{
    display: flex;  
    align-items: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    backdrop-filter: blur(10px);
    background-color: rgba(0,0,0,0.4);
}

.modal-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    margin-top: 70px;
    padding: 15px 15px 15px 15px;
    border-radius: 20px;
    position: relative;
    min-height: 100px;
    width: 280px;
    background-color: $backcolor;
}

.modal-inner {
    font-size: 20px;
    font-weight: 700;
}

.modal-inner .button-create .button-cancel{
    position: absolute;
    top: 16px;
    right: 16px;
}

.button-cancel{
    background-color: $backcolor;
    border: 2px solid $normal-gray;
    height: 48px;
    &:hover{
        background-color: $normal-gray;
    }
}


.modal-input {
    padding-left: 10px;
    border: 2px solid $normal-gray;
    border-radius: 10px;    
    height: 50px;
    outline: none;
    transition: 0.5s;
    &::placeholder{
        color: $bold-gray;
    }
    &:focus {
        border: 2px solid #6AABD2;
    }
    
}

