.AppBody {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-top: 20px;
  font-size: 20px;
  background-color: white;
  min-height: 100px;
  min-width: 400px;
  border-radius: 20px;
  border-style: none;
  padding: 20px 20px 20px 20px;
  transition: all 0.5s;
}
.AppBody:hover {
  box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.1);
}

@media all and (max-width: 450px) {
  .AppBody {
    min-width: 300px;
  }
}
@media all and (max-width: 350px) {
  .AppBody {
    min-width: 200px;
  }
}
@media all and (max-width: 300px) {
  .AppBody {
    min-width: 50px;
  }
}
.text {
  font-weight: 700;
  margin-top: 5px;
  color: #333333;
}

.button-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-family: Roboto;
  color: #333333;
  font-weight: 700;
  background-color: #dcdde0;
  border: none;
  border-radius: 10px;
  height: 50px;
  width: 100%;
  cursor: pointer;
  transition: 0.5s;
}
.button:hover {
  background-color: #c4c5c7;
}

.div-task {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.div-task-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 30px;
  margin-bottom: 30px;
}

.div-task-status {
  display: flex;
  align-items: center;
}

.div-task-title {
  display: flex;
  align-items: center;
  overflow: hidden;
}

.div-task-container {
  display: flex;
  gap: 10px;
}

.div-task-event {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.div-anytask {
  display: flex;
  justify-content: center;
  font-size: 20px;
  color: #c4c5c7;
}

.task {
  list-style-type: none;
  word-break: break-all;
  word-wrap: break-word;
  overflow-x: hidden;
  height: 30px;
  list-style-type: none;
}

.task-completed {
  text-decoration: line-through;
  opacity: 0.8;
}

svg {
  height: 25px;
  fill: #c4c5c7;
  fill-rule: evenodd;
  transition: 0.5s;
}

.svg-delete:hover {
  fill: #f4536b;
  fill-rule: evenodd;
}

.svg-edit:hover {
  fill: #6AABD2;
  fill-rule: evenodd;
}/*# sourceMappingURL=AppBody.css.map */